<template lang="pug">
    v-container
        collection-filter
        v-file-input(v-model="file" accept=".csv" show-size counter multiple label="选择上传数据表")
        Extend-Table

</template>

<script>
import { get } from 'vuex-pathify'
import * as fileapi from "@/apis/fileapi"
export default {
    data:()=>({
        file:[],
        hash_chunk_size:1024*1024*10,
        upload_chunk_size:1024*1024*2,
        filehash:"",
    }),
    computed: {
        filapath:get('db/uploaded_file_path'),
    },
    components: {
      CollectionFilter:() => import('@/components/DbCollectionFilter'),
      ExtendTable:() => import('@/components/DbUploadTable'),
    },
    methods:{
        upload_file_done_call_back(result){
            console.log("上传完成",result);
            if (result.result){
                this.$store.dispatch('db/update_uploaded_file',result.data);
            }
        }
    },
    watch:{
        file(){
            console.log(this.file[0])
            if (this.file.length>0){
                fileapi.GetFileMd5(this.file[0],this.hash_chunk_size).then(res=>{
                    this.filehash=res
                    this.$store.dispatch('db/update_file_hash',res);
                    if (this.filapath[res]){
                        console.log("文件已上传",this.filapath[res]);
                    }else{
                        fileapi.SendFile2Wsproxy(this.file[0],res,this.upload_chunk_size,this.upload_file_done_call_back);
                    }
                })
            }else{
                console.log("文件为空")
            }
        },
    }
}
</script>