// 文件操作API
import SparkMD5 from 'spark-md5';
const ws = require('./wsproxy');
// 获取文件Md5
export const GetFileMd5=(file,chunkSize)=>{
    return new Promise((resolve) => {
        let blobSlice = File.prototype.slice || File.prototype.mozSlice || File.prototype.webkitSlice;
        let chunks = Math.ceil(file.size / chunkSize);
        let currentChunk = 0;
        let spark = new SparkMD5.ArrayBuffer();
        let fileReader = new FileReader();
        fileReader.onload = function (e) {
            console.log('read chunk nr', currentChunk + 1, 'of', chunks);
            spark.append(e.target.result);                   // Append array buffer
            currentChunk++;
            if (currentChunk < chunks) {
                loadNext();
            } else {
                console.log('finished loading');
                let hash = spark.end();
                console.info('computed hash', hash);  // Compute hash
                resolve(hash)
            }
        };
        fileReader.onerror = function () {
            console.warn('oops, something went wrong.');
        };
        function loadNext() {
            var start = currentChunk * chunkSize,
                end = ((start + chunkSize) >= file.size) ? file.size : start + chunkSize;

            fileReader.readAsArrayBuffer(blobSlice.call(file, start, end));
        }
        loadNext();
    }).then(res=>{
        return res
    })
}

// 将文件发送给wsproxy 异步发送
export const SendFile2Wsproxy=function(file,hash,chunkSize,call_back){
    // 添加完成回调函数
    ws.AddCallBack(hash,call_back);
    let blobSlice = File.prototype.slice || File.prototype.mozSlice || File.prototype.webkitSlice;
    let chunks = Math.ceil(file.size / chunkSize);
    let currentChunk = 0;
    let fileReader = new FileReader();
    fileReader.onload = function (e) {
        console.log('发送消息到wsproxy chunk nr', currentChunk + 1, 'of', chunks);
        let params={
            action:"uploadfile",
            hash:hash,
            size:file.size,
            chunks:chunks,
            chunksize:chunkSize,
            index:currentChunk,
            filename:file.name,
            base64data:e.target.result,
        };
        ws.SendMsg(params);

        currentChunk++;
        if (currentChunk < chunks) {
            loadNext();
        } else {
            console.log("文件发送完成,共发送",chunks,"次")
        }
    };
    fileReader.onerror = function () {
        console.warn('oops, something went wrong.');
    };
    function loadNext() {
        var start = currentChunk * chunkSize,
            end = ((start + chunkSize) >= file.size) ? file.size : start + chunkSize;

        fileReader.readAsDataURL(blobSlice.call(file, start, end));
    }
    loadNext();
}
