import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home.vue'
import Login from '@/components/Login.vue'
import Lotout from '@/components/Logout.vue'
import Register from '@/components/Register.vue'
import  Resetpassword from '@/components/Resetpassword.vue'
import  UserInfoPage from '@/components/UserInfoPage.vue'
import TrxTokenHolders from '@/components/TrxTokenHolders.vue'
import TrxTransferHistory from '@/components/TrxTransferHistory.vue'
import TrxTransfer from '@/components/TrxTransfer.vue'
import DbView from '@/views/DbView.vue'
import DbUpload from '@/views/DbUpload.vue'
import DbConfig from '@/views/DbConfig.vue'
import PeBaseCharts from '@/views/PeBaseCharts.vue'
import PeProduction from '@/views/PeProduction.vue'
import PeProdVerify from '@/views/PeProdVerify.vue'
import PeWaterFlood from '@/views/PeWaterFlood.vue'

Vue.use(VueRouter)
  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/user',
    name: 'User',
    component: Home,
    children:[
      {path:'login',name:'Login',component:Login},
      {path:'logout',name:'Logout',component:Lotout},
      {path:'register',name:'register',component:Register},
      {path:'resetpassword',name:'resetpassword',component:Resetpassword},
      {path:'userinfo',name:'userinfo',component:UserInfoPage},
    ]
  },
  {
    path: '/trx',
    name: 'Trx',
    component: Home,
    children:[
      {path:'tokenholders',name:'TokenHolders',component:TrxTokenHolders},
      {path:'tokentransfers/:address',name:'TokenTransfers',component:TrxTransferHistory},
      {path:'transfer',name:'Transfer',component:TrxTransfer},
    ]
  },
  {
    path: '/db',
    name: 'Db',
    component: Home,
    children:[
      {path:'dbview',name:'DbView',component:DbView},
      {path:'dbupload',name:'DbUpload',component:DbUpload},
      {path:'dbconfig',name:'DbConfig',component:DbConfig}
    ]
  },
  {
    path: '/pe',
    name: 'Pe',
    component: Home,
    children:[
      {path:'pe_base_charts',name:'PeBaseCharts',component:PeBaseCharts},
      {path:'pe_prod_verify',name:'PeProdVerify',component:PeProdVerify},
      {path:'pe_production',name:'PeProduction',component:PeProduction},
      {path:'pe_water_flood',name:'PeWaterFlood',component:PeWaterFlood},
    ]
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
